<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <canvas id="viewStyle"></canvas>
    <img src="./assets/1473308168_167070.png">
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
export default {
  name: "app",
  mounted() {
    var winObject = window.screen;

    var width = (viewStyle.width = winObject.width);

    var height = (viewStyle.height = winObject.height);

    var letters = Array(256)
      .join(1)
      .split("");

    var draw = function() {
      viewStyle.getContext("2d").fillStyle = "rgba(0,0,0,.05)";

      viewStyle.getContext("2d").fillRect(0, 0, width, height);

      viewStyle.getContext("2d").fillStyle = "#0F0";

      letters.map(function(y_pos, index) {
        let text = String.fromCharCode(/*3e4*/ 65 + Math.random() * 33);

        let x_pos = index * 10;

        viewStyle.getContext("2d").fillText(text, x_pos, y_pos);

        letters[index] = y_pos > 758 + Math.random() * 1e4 ? 0 : y_pos + 10;
      });
    };

    setInterval(draw, 33);
  }
};
</script>

<style>
html {
  /* background: #000;   */
}
#viewStyle {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



